import { Component } from '@angular/core';
import { Transation } from './Models/Transaction';
import { ChartHelperServiceService } from 'src/app/Services/chart-helper-service.service';
import { WespacTransactionService } from 'src/app/Services/file-service.service';
import { TransactionServiceFactoryService } from 'src/app/Services/transaction-service-factory.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'xpens';
  bank :string ;

  cars = [        
    {"brand": "VW", "year": 2012, "color": "Orange", "vin": "dsad231ff"},
    {"brand": "Audi", "year": 2011, "color": "Black", "vin": "gwregre345"},
    {"brand": "Renault", "year": 2005, "color": "Gray", "vin": "h354htr"},
    {"brand": "BMW", "year": 2003, "color": "Blue", "vin": "j6w54qgh"},
    {"brand": "Mercedes", "year": 1995, "color": "Orange", "vin": "hrtwy34"},
    {"brand": "Volvo", "year": 2005, "color": "Black", "vin": "jejtyj"},
    {"brand": "Honda", "year": 2012, "color": "Yellow", "vin": "g43gr"},
    {"brand": "Jaguar", "year": 2013, "color": "Orange", "vin": "greg34"},
    {"brand": "Ford", "year": 2000, "color": "Black", "vin": "h54hw5"},
    {"brand": "Fiat", "year": 2013, "color": "Red", "vin": "245t2s"}
];

  data: any;
  options = {
    scales: {
      xAxes: [{
        display: true,
        aspectRatio : 2
      }]
    },
    layout: {
      padding: {
          left: 50,
          right: 0,
          top: 0,
          bottom: 0
      }
    },
    legend: {
      display: false,
  }
  };

  constructor(private charthelperService: ChartHelperServiceService,
              private transactionServiceFactory: TransactionServiceFactoryService) {
  }

  onFileSelected(files: FileList) {
    console.log(files);
    if (files && files.length > 0) {
      let file: File = files.item(0);
      const reader: FileReader = new FileReader();
      reader.readAsText(file);
      reader.onload = (e) => {
        let csv: string = reader.result as string;
        this.setChartData(csv);
      }
    }
  }
  selectBank(bank: any){
    this.bank = bank;
  }

  setChartData(fileContent: any) {
    const transactions = this.transactionServiceFactory.getTransactionService(this.bank).getTransactions(fileContent);
    this.data = {
      labels: ['January'],
      datasets: this.charthelperService.ToDataSet(transactions)
    }
  }
  refreshData(){
    this.options.legend.display = false;
  }
 
}
