import { ITransactionService } from '../Abstractions/ITransactionService';
import { Injectable } from '@angular/core';
import { Transation } from '../Models/Transaction';
import { extend } from 'webdriver-js-extender';

@Injectable({
  providedIn: 'root'
})
export class WespacTransactionService implements  ITransactionService {
  getTransactions(data: any): Transation[] {
    let lines = data.split('\n');
    let result: Transation[] = [];
    let headers = lines[0].split(",");
    for (let i = 1; i < lines.length - 1; i++) {
      const currentline = lines[i].split(",");
      result.push({
        date: currentline[1],
        item: currentline[2],
        amount: currentline[3]
      });
    }
    return result;
  }
  constructor() { }
}
