import { ITransactionService } from '../Abstractions/ITransactionService';
import { Injectable } from '@angular/core';
import { Transation } from '../Models/Transaction';
import { extend } from 'webdriver-js-extender';

@Injectable({
  providedIn: 'root'
})
export class CommbankTransactionService implements  ITransactionService {
  getTransactions(data: any): Transation[] {
    let lines = data.split('\n');
    let result: Transation[] = [];
    
    for (let i = 0; i < lines.length - 1; i++) {
      const currentline = lines[i].split(",");
      let amount1 = currentline[1].slice(2);
      amount1 = amount1.slice(0,amount1.length - 1)
      result.push({
        date: currentline[0],
        item: currentline[2],
        amount: amount1
      });
    }
    debugger;
    return result;
  }
  constructor() { }
}
