import { Injectable } from '@angular/core';
import { of, from } from 'rxjs';
import { groupBy, flatMap, toArray, mergeMap, map } from 'rxjs/operators';
import { Transation } from '../Models/Transaction';
@Injectable({
  providedIn: 'root'
})
export class ChartHelperServiceService {

  constructor() { }

  ToDataSet(transations: Transation[]): any {
    const data: any[] = [];
    const groupedbyItem = from(transations).pipe(
      groupBy(person => person.item),
      mergeMap(group => group.pipe(toArray()))
    );
    const subscribe = groupedbyItem.pipe(map(x => {
        const obj = {
          label: x[0].item,
          data: [x.map(y => +y.amount).reduce((acc, val) => acc + val)],
          backgroundColor: this.getRandomColor(),
          borderColor: '#1E88E5',
        };
        return obj;
      }
      )
    ).subscribe(val => data.push(val));

    debugger;
    return data;
  }
  getRandomColor() {
    const color = Math.floor(0x1000000 * Math.random()).toString(16);
    return '#' + ('000000' + color).slice(-6);
  }
}
