import { Injectable } from '@angular/core';
import { WespacTransactionService } from './file-service.service';
import { ITransactionService } from '../Abstractions/ITransactionService';
import { CommbankTransactionService } from './commbank-transactionservice';

@Injectable({
  providedIn: 'root'
})
export class TransactionServiceFactoryService {

  constructor() { }

  getTransactionService(bank: string): ITransactionService{
    debugger;
      if(bank ==="cwb")
        return new CommbankTransactionService();
        else
        return new WespacTransactionService();
  }
}
